import * as tf from '@tensorflow/tfjs';

/**
 * 
 */
export default class Model {
  /**
   * 
   */
  constructor() {
    this.inputs = [];
    this.labels = [];

    this.w1 = tf.variable(tf.randomNormal([3, 3]));
    this.b1 = tf.variable(tf.randomNormal([3]));
    this.w2 = tf.variable(tf.randomNormal([3, 2]));
    this.b2 = tf.variable(tf.randomNormal([2]));

    this.optimizer = tf.train.adam(0.1);
  }

  /**
   * 
   *
   * @param {Array} x
   * @return {Array} predY
   */
  model(x) {
    return tf.tidy(() => {
      return tf
        .tensor(x)
        .reshape([-1, 3])
        .matMul(this.w1)
        .add(this.b1)
        .matMul(this.w2)
        .add(this.b2);
    });
  }

  /**
   * 
   *
   * @param {Array} data  [x, ,  ]
   * @param {Boolean} label 
   *
   */
  train(data, label) {
    this.inputs.push(data);
    this.labels.push(label ? [1, 0] : [0, 1]);
    
    tf.tidy(() => {
      for (let i = 0; i < 30; i++) {
        this.optimizer.minimize(() => {
          const predYs = this.model(this.inputs);

          const loss = tf.losses.sigmoidCrossEntropy(
            tf.tensor2d(this.labels).reshape([-1, 2]),
            predYs,
          );
          // loss.data().then(l => console.log('Loss', l));
          return loss;
        });
      }
    });
  }

  /**
   * 
   *
   * @param {Array} data  [x, ,  ]
   * @return {Boolean} 
   */
  predict(data) {
    const predictRes = this.model(data).arraySync();
    return predictRes[0][0] > predictRes[0][1];
  }
}
